// Kleuren per land
const COUNTRY_COLORS: Record<string, { fill: string; stroke: string }> = {
  NL: { fill: 'rgba(255, 102, 0, 0.85)', stroke: '#CC5500' },      // Oranje
  BE: { fill: 'rgba(255, 205, 0, 0.85)', stroke: '#B8860B' },      // Geel
  DE: { fill: 'rgba(0, 0, 0, 0.75)', stroke: '#333333' },          // Zwart
  FR: { fill: 'rgba(0, 85, 164, 0.85)', stroke: '#003366' }        // Blauw
}

// Style voor fossiel hotspots
function createHotspotStyle(feature: Feature) {
  const name = feature.get('name') || ''
  const country = feature.get('country') || 'NL'
  const colors = COUNTRY_COLORS[country] || COUNTRY_COLORS.NL

  return new Style({
    image: new Circle({
      radius: 10,
      fill: new Fill({ color: colors.fill }),
      stroke: new Stroke({ color: colors.stroke, width: 2 })
    }),
    text: new Text({
      text: name,
      font: 'bold 11px sans-serif',
      offsetY: -18,
      fill: new Fill({ color: '#3E2723' }),
      stroke: new Stroke({ color: 'white', width: 3 }),
      textAlign: 'center'
    })
  })
}

export async function createFossielHotspotsLayerOL() {
  const features = FOSSIL_HOTSPOTS.map(spot => {
    const feature = new Feature({
      geometry: new Point(fromLonLat(spot.coords))
    })

    feature.setProperties({
      name: spot.name,
      type: spot.type,
      country: spot.country,
      finds: spot.finds,
      period: spot.period,
      geology: spot.geology,
      access: spot.access,
      tips: spot.tips,
      region: spot.region,
      layerType: 'fossielHotspot'
    })

    return feature
  })

  const source = new VectorSource({ features })

  const layer = new VectorLayer({
    source,
    properties: { title: 'Fossiel Hotspots' },
    visible: false,
    zIndex: 25,
    style: createHotspotStyle
  })

  console.log(`✓ Fossiel Hotspots loaded (${features.length} locations)`)
  return layer
}
